import { Component, Input, OnInit } from '@angular/core';
import { FetchClient } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-license-view',
  templateUrl: './license-view.component.html'
})
export class LicenseViewComponent implements OnInit {
  @Input()
  url: string;
  @Input()
  name: string;
  content: string = gettext('Loading license contentâ€¦');

  FALL_BACK_URL = 'https://spdx.org/licenses/';

  constructor(private fetchClient: FetchClient) {}

  async ngOnInit() {
    const response = await this.fetchClient.fetch(this.url);
    if (response.status === 200) {
      this.content = await response.text();
      return;
    }
    this.content = '';
  }
}
