import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { C8yStepper, gettext, OptionsService, WizardComponent } from '@c8y/ngx-components';
import { uniqBy } from 'lodash-es';
import { LicensedApplicationPlugin } from '@c8y/ngx-components/ecosystem/shared';
import { TranslateService } from '@ngx-translate/core';

interface ConfirmInstallModal {
  pluginsToInstall: LicensedApplicationPlugin[];
}

interface License {
  licenseName: string;
  url: string;
  appName: string;
}

@Component({
  selector: 'c8y-license-confirm-modal',
  templateUrl: './license-confirm-modal.component.html'
})
export class LicenseConfirmModalComponent implements OnInit {
  formGroupAccept: FormGroup;
  formGroupLicenses: FormGroup;
  @ViewChild(C8yStepper, { static: true })
  stepper: C8yStepper;
  licenses: License[] = [];
  disclaimerHtml: string;

  constructor(
    private wizardComponent: WizardComponent<ConfirmInstallModal>,
    private fb: FormBuilder,
    private optionsService: OptionsService,
    private translateService: TranslateService
  ) {}

  ngOnInit(): void {
    const { pluginsToInstall } = this.wizardComponent.componentInitialState;

    this.formGroupAccept = this.fb.group({
      confirmed: [false, Validators.required]
    });

    this.formGroupLicenses = this.fb.group({
      acceptAll: [false, Validators.required]
    });

    const allLicenses: License[] = pluginsToInstall.map(app => ({
      appName: app.name,
      url: `/apps/${app.contextPath}@${app.version}/LICENSE.txt`,
      licenseName: app.license
    }));

    const uniqByUrl = (uniqBy(allLicenses, 'url') as License[]).map(license => ({
      ...license,
      pluginName: allLicenses
        .filter(({ url }) => url === license.url)
        .map(({ appName: pluginName }) => pluginName)
        .join(', ')
    }));

    const companyName = this.optionsService.get('companyName', 'Cumulocity');
    const disclaimerHtmlWithCompany = gettext(
      'These plugins and/or blueprints are provided <strong>as-is and without warranty or support</strong>, and they do not constitute part of the {{ companyName }} product suite. Users are free to use subject to the license agreement.'
    );
    const disclaimerHtmlWithoutCompany = gettext(
      'These plugins and/or blueprints are provided <strong>as-is and without warranty or support</strong>, and they do not constitute part of the product suite. Users are free to use subject to the license agreement.'
    );

    this.disclaimerHtml = this.translateService.instant(
      companyName ? disclaimerHtmlWithCompany : disclaimerHtmlWithoutCompany,
      {
        companyName
      }
    );

    this.licenses = uniqByUrl;
  }

  cancel() {
    this.wizardComponent.close(false);
  }

  done() {
    this.wizardComponent.close(true);
  }
}
